
#app {
  font-family: Microsoft Yahei;
  text-align: center;
  margin-top: 250px;
}
body{
	background: url('./assets/bdbg.jpg');
}
.search-box{
	font-size: 0px;
}
.search-input{
	width:430px;
	height:45px;
	margin-top: 20px;
	padding: 3px 10px 3px 10px;
	border: 1px solid #ccc;
	box-sizing: border-box;
	outline: none;
	font-size: 18px;
}
.search-btn{
	width:100px;
	height:45px;
	border: 1px solid #38f;
	background-color: #38f;
	color:#fff;
	position: relative;
	top: -1px;
	outline: none;
	cursor: pointer;
}
.list{
	width:430px;
	height:30px;
	margin: 0 auto;
	padding-right: 100px;
}
.list>ul{
	margin:0;
	padding:0;
}
.list>ul>li{
	list-style: none;
	text-align: left;
	line-height: 30px;
	padding-left: 10px;
	background: #fff;
	font-size: 14px;
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
	box-sizing: border-box;
	cursor: pointer;
}
.list>ul>li:hover{
	background-color: #ccc;
}
.itemfade-enter,
.itemfade-leave-active {
    opacity: 0;
}
.itemfade-leave-active {
    position: absolute;
}



